.wrapper{
    margin: 50px auto;
    width: 400px;
}
.friendList{
    display: flex;
    flex-direction: column;
}
.item{
    display: flex;
    align-items: center;
    border: 1px solid black;
    border-radius: 5px;
    margin-bottom: 20px;
    padding: 10px;
    box-shadow: 3px 3px 3px grey;
}
.online{
    width: 20px;
    height: 20px;
    border: 1px solid black;
    border-radius: 50%;
    margin-right: 10px;
    background: green;
}
.offline{
    width: 20px;
    height: 20px;
    border: 1px solid black;
    border-radius: 50%;
    margin-right: 10px;
    background: red}

.avatar{
    border: 1px solid black;
    border-radius: 7px;
    margin-right: 15px;
}
.name{
    font-size: 35px;
    font-weight: bold;
}