*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}
.profile{
    margin: 125px auto;
    padding-top: 20px;
    width: 300px;
    border: 1px solid grey;
    border-radius: 5px;
}
.description{
    margin: 0 auto;
    padding: 25px 0;
    width: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: white;
}
.description img {
    height: 100px;
    width: 100px;
    border: 1px solid black;
    border-radius: 50%;
    padding: 10px;
    margin: 0 auto;
}
.description p {
    margin: 5px 0;
}
.name{
    margin: 15px 0;
    font-size: 25px;
    font-weight: bold;
}
.stats{
    display: flex;
    justify-content: center;
    align-items: center;
}
.stats li {
    display: flex;
    flex-direction: column;
    border: 1px solid grey;
    width: calc(100%/3);
    padding: 10px 0;
}