.transactionHistory{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    width: 800px;
    margin: 0 auto;
}
.head{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.items{
    width: 100%;
    display: flex;

}
.item{
    width: calc(100%/3);
    border: 1px solid black;
    padding: 15px;
}
.items:nth-child(2n){
    background: darkgrey;
}
.bkgbl{
    width: calc(100%/3);
    border: 1px solid black;
    padding: 15px;
    background: dodgerblue;
    color: white;
    text-transform: uppercase;
}